___CSS_LOADER_EXPORT___.push([module.id, ".scroller[data-v-58c56fff]{position:relative;flex:1 0;padding-top:20px;overflow-y:auto;overflow-x:hidden;border-bottom:1px solid var(--color-border);transition:all var(--animation-quick, 100ms) ease-in-out}.scroller--chatScrolledToBottom[data-v-58c56fff]{border-bottom-color:rgba(0,0,0,0)}.scroller__loading[data-v-58c56fff]{position:absolute;top:10px;left:50%;height:40px;width:40px;transform:translatex(-64px)}.messages-group__date[data-v-58c56fff]{display:block;text-align:center;padding-top:20px;position:relative;margin:20px 0}.messages-group__date-text[data-v-58c56fff]{margin-right:88px;content:attr(data-date);padding:4px 12px;left:50%;color:var(--color-text-maxcontrast);background-color:var(--color-background-dark);border-radius:var(--border-radius-pill)}.messages-group[data-v-58c56fff]:last-child{margin-bottom:16px}", "",{"version":3,"sources":["webpack://./src/components/MessagesList/MessagesList.vue","webpack://./src/assets/variables.scss"],"names":[],"mappings":"AAGA,2BACC,iBAAA,CACA,QAAA,CACA,gBAAA,CACA,eAAA,CACA,iBAAA,CACA,2CAAA,CACA,wDC6EY,CD3EZ,iDACC,iCAAA,CAGD,oCACC,iBAAA,CACA,QAAA,CACA,QAAA,CACA,WAAA,CACA,UAAA,CACA,2BAAA,CAKD,uCACC,aAAA,CACA,iBAAA,CACA,gBAAA,CACA,iBAAA,CACA,aAAA,CAGD,4CACC,iBAAA,CACA,uBAAA,CACA,gBAAA,CACA,QAAA,CACA,mCAAA,CACA,6CAAA,CACA,uCAAA,CAGD,4CACC,kBAAA","sourcesContent":["\n@import '../../assets/variables';\n\n.scroller {\n\tposition: relative;\n\tflex: 1 0;\n\tpadding-top: 20px;\n\toverflow-y: auto;\n\toverflow-x: hidden;\n\tborder-bottom: 1px solid var(--color-border);\n\ttransition: $transition;\n\n\t&--chatScrolledToBottom {\n\t\tborder-bottom-color: transparent;\n\t}\n\n\t&__loading {\n\t\tposition: absolute;\n\t\ttop: 10px;\n\t\tleft: 50%;\n\t\theight: 40px;\n\t\twidth: 40px;\n\t\ttransform: translatex(-64px);\n\t}\n}\n\n.messages-group {\n\t&__date {\n\t\tdisplay: block;\n\t\ttext-align: center;\n\t\tpadding-top: 20px;\n\t\tposition: relative;\n\t\tmargin: 20px 0;\n\t}\n\n\t&__date-text {\n\t\tmargin-right: $clickable-area * 2;\n\t\tcontent: attr(data-date);\n\t\tpadding: 4px 12px;\n\t\tleft: 50%;\n\t\tcolor: var(--color-text-maxcontrast);\n\t\tbackground-color: var(--color-background-dark);\n\t\tborder-radius: var(--border-radius-pill);\n\t}\n\n\t&:last-child {\n\t\tmargin-bottom: 16px;\n\t}\n}\n","/**\n * @copyright Copyright (c) 2019 John Molakvo <skjnldsv@protonmail.com>\n *\n * @author John Molakvo <skjnldsv@protonmail.com>\n *\n * @license GNU AGPL version 3 or any later version\n *\n * This program is free software: you can redistribute it and/or modify\n * it under the terms of the GNU Affero General Public License as\n * published by the Free Software Foundation, either version 3 of the\n * License, or (at your option) any later version.\n *\n * This program is distributed in the hope that it will be useful,\n * but WITHOUT ANY WARRANTY; without even the implied warranty of\n * MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n * GNU Affero General Public License for more details.\n *\n * You should have received a copy of the GNU Affero General Public License\n * along with this program. If not, see <http://www.gnu.org/licenses/>.\n *\n */\n\n@use 'sass:math';\n\n// https://uxplanet.org/7-rules-for-mobile-ui-button-design-e9cf2ea54556\n// recommended is 48px\n// 44px is what we choose and have very good visual-to-usability ratio\n$clickable-area: 44px;\n\n// background icon size\n// also used for the scss icon font\n$icon-size: 16px;\n\n// icon padding for a $clickable-area width and a $icon-size icon\n// ( 44px - 16px ) / 2\n$icon-margin: math.div($clickable-area - $icon-size, 2);\n\n// transparency background for icons\n$icon-focus-bg: rgba(127, 127, 127, .25);\n\n// popovermenu arrow width from the triangle center\n$arrow-width: 9px;\n\n// opacities\n$opacity_disabled: .5;\n$opacity_normal: .7;\n$opacity_full: 1;\n\n// menu round background hover feedback\n// good looking on dark AND white bg\n$action-background-hover: rgba(127, 127, 127, .25);\n\n// various structure data used in the\n// `NcAppNavigation` component\n$header-height: 50px;\n$navigation-width: 300px;\n\n// mobile breakpoint\n$breakpoint-mobile: 1024px;\n\n//messages list max width\n$messages-list-max-width: 800px;\n\n//message max width\n$message-max-width: 680px;\n\n//message utils width\n$message-utils-width: 100px;\n\n//message form max height\n$message-form-max-height: 180px;\n\n$color-call-background: rgba(34, 34, 34, 0.8);\n$color-guests-avatar: #ffffff;\n$color-background-guests-avatar: #b9b9b9;\n\n\n//chat font size\n$chat-font-size: 15px;\n\n//chat line height\n$chat-line-height: 1.6em;\n\n// transition\n$transition-duration-quick: var(--animation-quick, 100ms);\n$transition-duration-slow: var(--animation-slow, 300ms);\n\n$transition: all $transition-duration-quick ease-in-out;\n$transition-slow: all $transition-duration-slow ease-in-out;\n"],"sourceRoot":""}]);
