\n\t\t\tpadding: 0 8px 0 8px;\n\t\t}\n\t}\n\n\t&__scroll {\n\t\tposition: absolute;\n\t\ttop: 0;\n\t\tright: 0;\n\t\twidth: fit-content;\n\t\theight: 100%;\n\t\tpadding: 8px 8px 0 0;\n\t}\n\n\t&__reactions {\n\t\tdisplay: flex;\n\t\tflex-wrap: wrap;\n\t\tmargin: 4px 175px 4px -2px;\n\t}\n}\n\n.date {\n\tmargin-right: $clickable-area;\n\t&--self {\n\t\tmargin-right: 0;\n\t}\n}\n\n// Increase the padding for regular messages to improve readability and\n// allow some space for the reply button\n.message-body:not(.system) {\n\tpadding: 4px 4px 4px 8px;\n}\n\n.message--highlighted {\n\tanimation: highlight-animation 5s 1;\n\tborder-radius: 8px;\n}\n\n@keyframes highlight-animation {\n\t0% { background-color: var(--color-background-hover); }\n\t50% { background-color: var(--color-background-hover); }\n\t100% { background-color: rgba(var(--color-background-hover), 0); }\n}\n\n.new-message-marker {\n\tposition: relative;\n\tmargin: 20px 15px 20px -45px;\n\tborder-top: 1px solid var(--color-border);\n\n\tspan {\n\t\tposition: absolute;\n\t\ttop: 0;\n\t\tleft: 50%;\n\t\ttransform: translateX(-50%) translateY(-50%);\n\t\tpadding: 0 7px 0 7px;\n\t\ttext-align: center;\n\t\twhite-space: nowrap;\n\n\t\tborder-radius: var(--border-radius);\n\t\tbackground-color: var(--color-main-background);\n\t}\n}\n\n.message-status {\n\twidth: $clickable-area;\n\theight: 24px;\n\tdisplay: flex;\n\tjustify-content: center;\n\talign-items: center;\n\n\t&.retry-option {\n\t\tcursor: pointer;\n\t}\n}\n\n.reaction-button {\n\t// Clear server rules\n\tmin-height: 0 !important;\n\t:deep(.button-vue__text) {\n\t\tfont-weight: normal !important;\n\t}\n\n\tmargin: 2px;\n\theight: 26px;\n\tpadding: 0 6px !important;\n\n\t&__emoji {\n\t\tmargin: 0 4px 0 0;\n\t}\n}\n\n.reaction-details {\n\tpadding: 8px;\n}\n\n.message-buttons-bar {\n\tdisplay: flex;\n\tright: 14px;\n\ttop: 8px;\n\tposition: sticky;\n\tbackground-color: var(--color-main-background);\n\tborder-radius: calc(var(--default-clickable-area) / 2);