___CSS_LOADER_EXPORT___.push([module.id, ".avatar-32px[data-v-5330e1c8]{position:sticky;top:0;width:32px;height:32px;line-height:32px;font-size:16px;border-radius:50%}.avatar-32px.icon[data-v-5330e1c8]{border-radius:50%;height:32px;width:32px;background-color:var(--color-background-darker)}.avatar-32px.bot[data-v-5330e1c8]{padding-left:5px;line-height:32px;border-radius:50%;background-color:var(--color-background-darker)}.avatar-32px.guest[data-v-5330e1c8]{line-height:32px;font-size:16px;color:#fff;background-color:#b9b9b9;padding:0;display:block;text-align:center;margin-left:auto;margin-right:auto}", "",{"version":3,"sources":["webpack://./src/assets/avatar.scss","webpack://./src/components/MessagesList/MessagesGroup/AuthorAvatar.vue","webpack://./src/assets/variables.scss"],"names":[],"mappings":"AA2BC,8BACC,eAAA,CACA,KAAA,CACA,UC1BmB,CD2BnB,WC3BmB,CD4BnB,gBC5BmB,CD6BnB,cAAA,CACA,iBAAA,CAEA,mCACC,iBAAA,CACA,WClCkB,CDmClB,UCnCkB,CDoClB,+CAAA,CAGD,kCACC,gBAAA,CACA,gBCzCkB,CD0ClB,iBAAA,CACA,+CAAA,CAGD,oCACC,gBC/CkB,CDgDlB,cAAA,CACA,UEoBmB,CFnBnB,wBEoB8B,CFnB9B,SAAA,CACA,aAAA,CACA,iBAAA,CACA,gBAAA,CACA,iBAAA","sourcesContent":["/**\n * @copyright Copyright (c) 2020 Joas Schilling <coding@schilljs.com>\n *\n * @author Joas Schilling <coding@schilljs.com>\n *\n * @license GNU AGPL version 3 or any later version\n *\n * This program is free software: you can redistribute it and/or modify\n * it under the terms of the GNU Affero General Public License as\n * published by the Free Software Foundation, either version 3 of the\n * License, or (at your option) any later version.\n *\n * This program is distributed in the hope that it will be useful,\n * but WITHOUT ANY WARRANTY; without even the implied warranty of\n * MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n * GNU Affero General Public License for more details.\n *\n * You should have received a copy of the GNU Affero General Public License\n * along with this program. If not, see <http://www.gnu.org/licenses/>.\n *\n */\n@use 'sass:math';\n\n@import 'variables';\n\n\n@mixin avatar-mixin($size: 32px) {\n\t.avatar-#{$size} {\n\t\tposition: sticky;\n\t\ttop: 0;\n\t\twidth: $size;\n\t\theight: $size;\n\t\tline-height: $size;\n\t\tfont-size: math.div($size, 2);\n\t\tborder-radius: 50%;\n\n\t\t&.icon {\n\t\t\tborder-radius: 50%;\n\t\t\theight: $size;\n\t\t\twidth: $size;\n\t\t\tbackground-color: var(--color-background-darker);\n\t\t}\n\n\t\t&.bot {\n\t\t\tpadding-left: 5px;\n\t\t\tline-height: $size;\n\t\t\tborder-radius: 50%;\n\t\t\tbackground-color: var(--color-background-darker);\n\t\t}\n\n\t\t&.guest {\n\t\t\tline-height: $size;\n\t\t\tfont-size: math.div($size, 2);\n\t\t\tcolor: $color-guests-avatar;\n\t\t\tbackground-color: $color-background-guests-avatar;\n\t\t\tpadding: 0;\n\t\t\tdisplay: block;\n\t\t\ttext-align: center;\n\t\t\tmargin-left: auto;\n\t\t\tmargin-right: auto;\n\t\t}\n\t}\n}\n","\n@import '../../../assets/avatar';\n\n// size of avatars of chat message authors\n$author-avatar-size: 32px;\n@include avatar-mixin($author-avatar-size);\n\n","/**\n * @copyright Copyright (c) 2019 John Molakvo <skjnldsv@protonmail.com>\n *\n * @author John Molakvo <skjnldsv@protonmail.com>\n *\n * @license GNU AGPL version 3 or any later version\n *\n * This program is free software: you can redistribute it and/or modify\n * it under the terms of the GNU Affero General Public License as\n * published by the Free Software Foundation, either version 3 of the\n * License, or (at your option) any later version.\n *\n * This program is distributed in the hope that it will be useful,\n * but WITHOUT ANY WARRANTY; without even the implied warranty of\n * MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n * GNU Affero General Public License for more details.\n *\n * You should have received a copy of the GNU Affero General Public License\n * along with this program. If not, see <http://www.gnu.org/licenses/>.\n *\n */\n\n@use 'sass:math';\n\n// https://uxplanet.org/7-rules-for-mobile-ui-button-design-e9cf2ea54556\n// recommended is 48px\n// 44px is what we choose and have very good visual-to-usability ratio\n$clickable-area: 44px;\n\n// background icon size\n// also used for the scss icon font\n$icon-size: 16px;\n\n// icon padding for a $clickable-area width and a $icon-size icon\n// ( 44px - 16px ) / 2\n$icon-margin: math.div($clickable-area - $icon-size, 2);\n\n// transparency background for icons\n$icon-focus-bg: rgba(127, 127, 127, .25);\n\n// popovermenu arrow width from the triangle center\n$arrow-width: 9px;\n\n// opacities\n$opacity_disabled: .5;\n$opacity_normal: .7;\n$opacity_full: 1;\n\n// menu round background hover feedback\n// good looking on dark AND white bg\n$action-background-hover: rgba(127, 127, 127, .25);\n\n// various structure data used in the\n// `NcAppNavigation` component\n$header-height: 50px;\n$navigation-width: 300px;\n\n// mobile breakpoint\n$breakpoint-mobile: 1024px;\n\n//messages list max width\n$messages-list-max-width: 800px;\n\n//message max width\n$message-max-width: 680px;\n\n//message utils width\n$message-utils-width: 100px;\n\n//message form max height\n$message-form-max-height: 180px;\n\n$color-call-background: rgba(34, 34, 34, 0.8);\n$color-guests-avatar: #ffffff;\n$color-background-guests-avatar: #b9b9b9;\n\n\n//chat font size\n$chat-font-size: 15px;\n\n//chat line height\n$chat-line-height: 1.6em;\n\n// transition\n$transition-duration-quick: var(--animation-quick, 100ms);\n$transition-duration-slow: var(--animation-slow, 300ms);\n\n$transition: all $transition-duration-quick ease-in-out;\n$transition-slow: all $transition-duration-slow ease-in-out;\n"],"sourceRoot":""}]);
